body {
  background: #222;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
  margin: 0;
  padding: 20px 0;
  background-color: whitesmoke
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.app {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

form.uploadForm {
  margin-bottom: 30px;
}

form.uploadForm input {
  padding: 15px 10px;
  display: block;
  margin: 10px auto;
}

.btn {
  border: 2px solid gray;
  color: gray;
  background-color: white;
  padding: 8px 20px;
  border-radius: 8px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
}

input.btn[type=text] {
  cursor: text;
  width: 50vw;
}

input.btn[type=submit], form.uploadForm input[type=file] {
  width: 20vw !important;
}

input.btn[type=submit] {
  background: whitesmoke;
}

input.btn[type=submit]:hover {
  -webkit-box-shadow: 0px 0px 0px 3px #fff;
          box-shadow: 0px 0px 0px 3px #fff;
}

.upload-btn-wrapper input[type=file] {
  font-size: 100px;
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
}
::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: #ccc;
}
::-moz-placeholder { /* Firefox 19+ */
  color: #ccc;
}
:-ms-input-placeholder { /* IE 10+ */
  color: #ccc;
}
:-moz-placeholder { /* Firefox 18- */
  color: #ccc;
}

.form-section {
  width: 60%;
  margin: 0 auto;
  background: #222;
  color: #fff;
  text-align: center;
}

.form-element {
  padding: 3vh;
}
.form-section {
  width: 100%;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 0 auto;
  background: #222;
  color: #fff;
}

.json-output {
  color: white;
}


